@use '../abstracts/' as *;

.form {
  width: 100%;
  padding-top: 2rem;
  
  display: flex;
  flex-direction: column;

  @include breakpoint(laptop){
    padding-top: 0;
  }

&__group {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-block: 0.5rem;
}

  &__label {
    font-size: 12px;
    text-transform: uppercase;
    font-weight: var(--fw-600);
    padding-block: 0.5rem;
  }

  &__input {
    padding-block: 0.5rem;
    padding-left: 1rem;
    border-radius: 4px;
  }

  &__textarea{
      height: 7rem;
      resize: none;
      padding:  .5rem 1rem;
      border-radius: 4px;
      margin-top: 1rem;

      @include breakpoint(tablet){
        height: 10rem;
        margin-top: 1.5rem;
        
      }
  }

  &__btn{
      margin-top: 1rem;
      width: 100%;

      @include breakpoint(tablet){
        margin-top: 1.5rem;
      }
  }
}
